import React from 'react';
import config from '../config';
import api from '../lib/api';
import styles from './index.css';
import Headline from '../components/headline/headline';
import Button from '../components/button/button';
import Tools from '../components/tools/tools';
import Spacer from '../components/spacer/spacer';
import ButtonGroup from '../components/buttongroup/buttongroup';
import RichText from '../components/richtext/richtext';
import Team from '../components/team/team';
import Buzzwords from '../components/buzzwords/buzzwords';
import Link from '../components/link/link';
import Subline from '../components/subline/subline';

import Paragraph from '../components/paragraph/paragraph';
import List from '../components/list/list';
import Frame from '../components/frame/frame';

type Props = {
  team: Array<{ img: string; name: string; description: string }>;
  buzzwords: string;
  tools: Array<{ headline: string; text: string }>;
  ogImage: { _src: string };
  headline: string;
  pageTitle: string;
  lasseText: string;
  hannesText: string;
  pageDescription: string;
  subline: string;
  steps: string[];
  references: string[];
  partners: string[];
  headerbuttons: Array<{ label: string; href: string }>;
  missionText: string;
};

export default function Index(p: Props) {
  return (
    <Frame title={p.pageTitle} description={p.pageDescription}>
      <main>
        <header className={styles.header}>
          <div className={styles.headerText}>
            <Headline level={1}>{p.headline}</Headline>
            <Subline>{p.subline}</Subline>
          </div>
          <ButtonGroup data={p.headerbuttons} />
        </header>

        <RichText className={styles.mission}>{p.missionText}</RichText>
        <Link href="/arbeitsweise">Erz&auml;hl mir mehr</Link>
        <Spacer size={3} />
        <Headline level={2}>Werkzeuge</Headline>
        <Tools data={p.tools} />
        <Buzzwords headline="Mehr buzz?">{p.buzzwords}</Buzzwords>
        <Headline level={2}>Team</Headline>

        <Team data={p.team} />
        <List headline="Referenzen" data={p.references} />
      </main>
    </Frame>
  );
}

Index.getInitialProps = () => api('Startpage');
